import React, {useEffect, useState} from "react";
import {connect} from "react-redux";
import {UnControlled as CodeMirror} from 'react-codemirror2'
import {Row, Col, Descriptions, Button} from 'antd';
import Actions from "../../../actions/Actions";
import 'codemirror/lib/codemirror.js'
import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/material-darker.css';
import 'codemirror/mode/yaml/yaml';
import queryString from "query-string";

const Kubernetes = (props) => {
    const {loading, helmValesYaml, getHelmValueYaml, deployChaostoolsToK8S} = props
    const [name, setName] = useState()
    const [version, setVersion] = useState()
    const [valuesYaml, setValuesYaml] = useState("")

    useEffect(() => {

        const parsed = queryString.parse(window.location.search);
        const {name, version} = parsed;
        setName(name)
        setVersion(version)
        getHelmValueYaml(name, version)
    }, [])

    useEffect(() => {
        if (valuesYaml === "") {
            setValuesYaml(helmValesYaml)
        }
    })

    return (
        <>
            <Button loading={loading} type="primary" onClick={() => {
                deployChaostoolsToK8S(name, version, valuesYaml)
            }} style={{marginBottom: 5}}>安装</Button>

            <Row>
                <Col span={24}>

                    <CodeMirror
                        value={helmValesYaml}
                        options={{
                            mode: 'yaml',
                            theme: 'material-darker',
                            lineNumbers: true
                        }}
                        onChange={(editor, data, value) => {
                            setValuesYaml(value)
                        }}
                    />
                </Col>
                <Col span={24}>
                    <Descriptions title="Uninstall" style={{marginTop: 10}}>
                        <Descriptions.Item label="Delete crd" span={24}>kubectl delete crd chaosblades.chaosblade.io</Descriptions.Item>
                        <Descriptions.Item label="Delete operator" span={24}>helm delete xxxx</Descriptions.Item>
                    </Descriptions>
                </Col>
            </Row>


        </>
    )
}


export default connect(
    state => {
        const chaostoolsDeploy = state.chaostoolsDeploy.toJS();
        const {helmValesYaml, installLoading} = chaostoolsDeploy
        return {
            helmValesYaml: helmValesYaml,
            loading: installLoading,
        };
    },
    (
        dispatch,
    ) => {
        return {
            getHelmValueYaml: (name, version) => {
                dispatch(Actions.getHelmValueYaml(name, version));
            },
            deployChaostoolsToK8S: (name, version, valuesYaml) => {
                dispatch(Actions.deployChaostoolsToK8S(name, version, valuesYaml));
            },
        };
    }
)(Kubernetes);
